import Component from "jx/core/comps/Component";
export default class MenuEditionTop extends Component {
    constructor(dataObject) {
        super(Object.assign({}, dataObject));
        this.view = $("#MainTopMenu");
        this.view.css("background-color", this.jx.config.app.pageBackgroundColor);
        this.pageTabs = $("<div id='pagesTabs'/>");
        this.pageTabs.css("top", "0px");
        $("body").append(this.pageTabs);
        this.view.css("display", "block");
    }
    setMenu(menu) {
        this.menu = menu;
        this.view.prepend(this.menu.view);
    }
    setPageTabs(pageTabs) {
        this.pageTabs.empty();
        this.pageTabs.append(pageTabs);
    }
    showComponent(id) {
        this.view.find("#" + id).css("display", "block");
    }
    hideComponent(id) {
        this.view.find("#" + id).css("display", "none");
    }
    kill() {
        super.kill();
    }
}
